@import url(https://fonts.googleapis.com/css2?family=Mr+Bedfort&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Texturina:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oleo+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Some exciting button focus styles */
*:focus { 
  outline: rgb(97, 106, 185) solid 2px;
}
/* Undo all the above focused button styles
  if the button has focus but the browser wouldn't normally
  show default focus styles */
*:focus:not(:focus-visible) {
  outline: none;
}
/* Some even *more* exciting button focus styles */
*:focus-visible { 
  outline: rgb(97, 106, 185) solid 2px;
}

.nye p{color:#d3d3d3;font-size:1rem;letter-spacing:1px;line-height:2;font-family:"Texturina";white-space:pre-wrap}.nye .jumbotron.jumbotron-fluid{height:100vh;background-color:transparent;text-align:center;display:flex;flex-direction:column;justify-content:center}.nye .jumbotron.jumbotron-fluid h1{font-family:"Mr Bedfort", cursive;white-space:pre-wrap;text-align:center;vertical-align:middle;font-size:7rem;letter-spacing:3px;color:#fffbe6;text-shadow:0 0 5px #ffffcc, 0 0 8px #ffb500, 0 0 11px yellow}.nye .jumbotron.jumbotron-fluid img{max-width:100%}.nye .letter-text{margin-bottom:15vh}.nye .gallery-memory-lane{display:flex;align-items:center;margin:15vh 0}.nye .gallery-memory-lane img{max-width:100%}.nye .cursor{position:absolute;animation-name:spin;animation-duration:500ms;animation-timing-function:linear;animation-iteration-count:infinite;pointer-events:none;transition-timing-function:ease-out;transition-delay:0}.nye .cursor.cursor1{width:90px;height:90px;transition-duration:0}.nye .cursor.cursor1 img{width:90px;height:90px}.nye .cursor.cursor2{width:50px;height:50px;transition-duration:50ms}.nye .cursor.cursor2 img{width:50px;height:50px}.nye .cursor.cursor3{width:50px;height:50px;transition-duration:54ms}.nye .cursor.cursor3 img{width:50px;height:50px}.nye .cursor.cursor4{width:30px;height:30px;transition-duration:51ms}.nye .cursor.cursor4 img{width:30px;height:30px}.nye .cursor.cursor5{width:20px;height:20px;transition-duration:42ms}.nye .cursor.cursor5 img{width:20px;height:20px}.nye .cursor.cursor6{width:35px;height:35px;transition-duration:53ms}.nye .cursor.cursor6 img{width:35px;height:35px}@keyframes spin{to{transform:rotate(0deg)}from{transform:rotate(360deg)}}@media (max-device-width: 760px){.nye .gallery-memory-lane.reverse{flex-direction:column-reverse}.nye .cursor{display:none}}

html,body,.xmas.container{width:100vw;min-height:100vh;overflow-x:hidden}.xmas.container{flex-direction:column;display:flex;justify-content:center;padding:0 12vw}.xmas.container p{font-size:1rem}.xmas.container .banner-words{text-align:center;min-height:100vh;display:flex;justify-content:center;flex-direction:column}.xmas.container .banner-words h1{font-family:"Oleo Script", cursive;color:#f5f7da;white-space:pre-wrap;font-size:6rem;text-shadow:5px 0 #960000;padding:5vw}.xmas.container .banner-words p{line-height:1;font-family:monospace;color:#f5f7da;letter-spacing:1px;white-space:pre-wrap}.xmas.container .letter-content{margin-bottom:10vh;white-space:pre-wrap}.xmas.container .letter-content p,.xmas.container .gallery-memory-lane p{font-family:"Permanent Marker", cursive;color:#f5f7da;letter-spacing:1px;line-height:3}.xmas .santa-holder img{position:relative;left:-100vw;animation-name:santa-go-brr;animation-play-state:running;animation-duration:15000ms;animation-timing-function:linear;animation-iteration-count:infinite}@keyframes santa-go-brr{from{position:relative;left:-100vw}to{position:relative;left:110vw}}.xmas .gallery-memory-lane{display:flex;align-items:center;margin:2vh 5vh}.xmas img{max-width:100%}@media (max-device-width: 760px){.xmas .gallery-memory-lane.reverse{flex-direction:column-reverse}}.xmas .hat-rack{display:flex;justify-content:space-between;flex-wrap:wrap;position:absolute;top:-200vh;max-width:100vw;padding:0 5vw}.xmas .hat-rack .hat{max-width:25vw;max-height:25vw;animation-name:hat-falling;animation-timing-function:linear;position:relative;top:-25vw}.xmas .hat-rack .hat.hat-1{animation-duration:1200ms;animation-delay:400ms}.xmas .hat-rack .hat.hat-2{animation-duration:1700ms;animation-delay:500ms}.xmas .hat-rack .hat.hat-3{animation-duration:2000ms;animation-delay:100ms}.xmas .hat-rack .hat.hat-4{animation-duration:1700ms}.xmas .hat-rack .hat.hat-5{animation-duration:1300ms;animation-delay:200s}.xmas .hat-rack .hat.hat-6{animation-duration:1000ms;animation-delay:300ms}.xmas .hat-rack .hat.hat-7{animation-duration:2000ms;animation-delay:600ms}.xmas .hat-rack .hat.hat-8{animation-duration:1050ms;animation-delay:100ms}.xmas .hat-rack .hat.hat-9{animation-duration:1100ms;animation-delay:900ms}.xmas .hat-rack .hat.hat-10{animation-duration:2000s;animation-delay:800s}@keyframes hat-falling{0%{top:-100px;transform:rotate(0deg)}33%{transform:rotate(-40deg)}66%{transform:rotate(40deg)}100%{top:300vh;transform:rotate(0deg)}}.xmas img.lights{position:fixed;width:10vw;top:0vh}.xmas img.lights.left{left:0}.xmas img.lights.right{right:0}@media (max-device-width: 440px) and (min-device-height: 360px){.xmas.xmas.container .banner-words h1{font-size:2.5rem}.xmas.xmas.container .letter-content p,.xmas.xmas.container .gallery-memory-lane p{font-size:1rem;line-height:2}}

